.call-button {
    font-family: Beausite-regular, sans-serif;
    padding: 8px 24px;
    background-color: #5048E5;
    border-radius: 8px;
    color: #FFFFFF;
    font-size: 22px;
    cursor: pointer;
    border: none;
    margin: 0;
    text-align: inherit;
    appearance: none;
}

.light-button {
    background-color: #FFFFFF;
    color: #5048E5;
    padding: 16px 32px;
}